.input {
  background: var(--base-gray-600, #1e1e29);
  border-radius: 0.5rem;
  width: 100%;
  padding: 1rem;
  &:focus {
    outline: none;
    &::placeholder {
      color: transparent;
    }
  }
  &::placeholder {
    transition: color 0.2s;
    color: var(--base-gray-400, #7f7f98);
  }
}
