.wrapper {
  width: 100vw;
  height: 100vh;
  padding: 2rem;
  background-image: url("../../assets/bg.png");
}

.container {
  height: 55vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
// logo
.logoContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}
.logoIcon {
  height: 4rem;
  width: 4rem;
}

.logoText {
  font-size: 1.5rem;
}
// content
.searchText {
  margin-bottom: 3.5rem;
}
.title {
  color: var(--base-gray-100, #fafafa);
  font-size: 2rem;
  font-weight: 700;
  a {
    color: var(--product-blue-light, #8fb2f5);
    &:hover {
      text-decoration: underline;
    }
  }
}
.desc {
  color: var(--base-gray-200, #bfbfd4);
}

.keyInput {
  color: #000;
}
