@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;700;800&family=Roboto+Condensed:wght@300;400;500;700&family=Roboto:wght@400;500;700&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
  /* font-family: 'Roboto', sans-serif; */
  color: #fff;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  outline: none;
}

* {
  box-sizing: border-box;
  border: none;
  color: inherit;
  margin: 0;
  padding: 0;
}
