.wrapper {
  display: flex;
  height: 100vh;
  display: flex;
  padding: 1.5rem;
  background: var(--base-gray-900, #13131a);
  gap: 1.5rem;
  & > div {
    flex: 1;
  }
}

.card {
  height: 100%;
  min-width: 52%;
  border-radius: 0.75rem;
  background: var(--base-gray-800, #16161f);
  display: flex;
  flex-direction: column;
  padding: 1rem;
  gap: 1rem;
}

.header {
  display: flex;
  gap: 1rem;
}

.logo {
  width: fit-content;
  padding: 0.5rem;
  border-radius: 0.5rem;
  background: var(--base-gray-600, #1e1e29);
  &:hover {
    cursor: pointer;
  }
  img {
    height: 2.5rem;
  }
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  border-radius: 0.5rem;
  background-size: cover;
}
.infoContainer {
  display: flex;
  justify-content: space-between;
  margin: 2rem;
  gap: 0.5rem;
  .place {
    font-size: 1.25rem;
    font-weight: 700;
  }
  .date {
    font-size: 1rem;
    font-weight: 400;
  }
}

.time {
  font-size: 1.25rem;
  font-weight: 700;
}

.addData {
  display: flex;
  justify-content: space-between;
}

.temperature {
  font-size: 6rem;
  font-weight: 800;
}

.weather {
  margin: 2rem;
}

.desc {
  font-size: 1.25rem;
}

.weatherLogo {
  width: 15.5rem;
  height: 15.5rem;
}

.rightSide {
  display: flex;
  flex-direction: column; /* Розташування дітей у стовпці */

  gap: 1.5rem;
  width: 40%;
  height: 100%; /* Робимо розтягнутим на всю висоту */
}
.weatherDetails,
.details {
  flex: 1;
  border-radius: 0.75rem;
  background: var(--base-gray-800, #16161f);
  padding: 1.75rem 1.5rem 0.5rem 1.5rem;
}
.detaildsTitle {
  color: var(--base-gray-400, #7f7f98);
  font-size: 1rem;
  font-weight: 400;
}

ul li {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid var(--base-gray-700, #1c1c27);
  padding: 1rem;
  &:last-child {
    border-bottom: none;
  }
  div {
    display: flex;
    align-items: center;
    img {
      margin-right: 0.5rem;
    }
  }
}

.forecastTitle {
  margin-bottom: 3rem;
}

.forecastList {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
